import {graphql, withPrefix} from 'gatsby';
import Img from 'gatsby-image';
import React from 'react';

import {Button} from '../../../components/button';
import {CaseHeader} from '../../../components/case_header';
import {WillAnimate} from '../../../components/content_animate';
import {Layout} from '../../../components/layout';
import {ScrollTo} from '../../../components/scroll_to';

const locale = 'en';
const translatedPagePath = '/nl/portfolio/nohrd-bike-apps/';

export default ({data}: any) => (
    <Layout
        title="Case: Apps for NOHrD Bike – Premium software for a premium exercise bike – Showcase of our work"
        description="Native applications to make the experience on the NOHrD Bike even better. Read about how our team worked on the development of the NOHrD Bike apps."
        ogImage="nohrd-bike.png"
        translatedPagePath={translatedPagePath}
        navColor="white"
        locale={locale}>
        <CaseHeader
            urlBack="/en/portfolio/valuation-platform-taxapi/"
            titleBack="Case: Valuation platform TaXapi"
            urlForward="/en/portfolio/mobile-road-signaling-tti-tactic/"
            titleForward="Case: Mobile road signaling TTI TACTIC"
            translatedPagePath={translatedPagePath}
            locale={locale}>
            <Img className="hero__image" alt="NOHrD bike" fluid={data.nohrdBike.childImageSharp.fluid} critical />
            <div className="hero__content-wrapper">
                <div className="hero__content">
                    <h1>
                        {/* Make sure to have the correct spacing when br becomes invisible */}
                        Cycling through <br />
                        the Alps at home
                    </h1>
                    <h2>Premium software for a premium home trainer.</h2>
                </div>
            </div>
            <ScrollTo className="hero__scroll" target="/en/portfolio/nohrd-bike-apps/#from-hero" />
        </CaseHeader>

        <section className="section">
            {/* Case intro */}
            <WillAnimate as="article" className="content">
                <div className="container">
                    <div className="content__display content__display--intro">
                        <h2>
                            Together with NOHrD we develop <span className="highlight">unique apps</span> for the Bike.
                            Giving the end user a high-end home trainer <span className="highlight">enriched</span> with
                            an app packed with features, coaching and experiences.
                        </h2>
                    </div>
                </div>
            </WillAnimate>

            {/* Case assignment */}
            <WillAnimate as="article" id="from-hero" className="content">
                <div className="container">
                    <div className="content__display content__display--right">
                        <h2>The assignment</h2>
                    </div>

                    <div className="content__copy">
                        <p className="p-lead">
                            The Bike was designed with <span className="highlight">apps</span> in mind. An app means the
                            cycling experience can keep improving&mdash;continuing long after the purchase is made. To
                            make this possible, the Bike features a clever system for mounting any size of tablet or
                            smartphone.
                        </p>
                        <p>
                            NOHrD asked us to develop <span className="highlight">native</span> iPhone, iPad and Android
                            applications to <span className="highlight">enrich</span> the new indoor cycle.
                        </p>
                        <p>
                            The software needed to be an elegant complement to the physical product, with the emphasis
                            on experiencing the product <span className="highlight">as a single unit</span>. One of the
                            consequences of this requirement was that the wooden bicycle needed to be able to make
                            connection without any fumbling with wires or awkward plug sockets.
                        </p>
                        <ul className="list">
                            <li className="ion-md-arrow-dropright">UX and UI design</li>
                            <li className="ion-md-arrow-dropright">Native iOS and Android development</li>
                            <li className="ion-md-arrow-dropright">Technical product advice</li>
                            <li className="ion-md-arrow-dropright">Enriching a physical product with software</li>
                        </ul>
                    </div>
                </div>
            </WillAnimate>

            {/* Article image and role */}

            <WillAnimate as="article" className="content">
                <div className="image">
                    <div className="image__content">
                        <video
                            width="100%"
                            controls
                            autoplay="autoplay"
                            loop
                            muted
                            poster={withPrefix('/images/nohrd-bike-placeholder.png')}>
                            <source src={withPrefix('/video/nohrd-bike.mp4')} type="video/mp4" />
                        </video>
                    </div>
                    <div className="image__caption">
                        <span className="ion-md-arrow-dropright">
                            The NOHrD Bike app is available for iPhone, iPad and Android, and integrates with various
                            heart rate sensors for extra information while cycling.
                        </span>
                    </div>
                </div>
            </WillAnimate>

            {/* Case challenge */}
            <WillAnimate as="article" className="content">
                <div className="container">
                    <div className="content__display">
                        <h2>The challenge</h2>
                    </div>

                    <div className="content__copy">
                        <p className="p-lead">
                            In the first phase of product development we needed to{' '}
                            <span className="highlight">work closely</span> with NOHrD’s product team to achieve a
                            seamless connection between the hardware and the software. We wanted to use the right number
                            of sensors to collect as much session data as possible to provide{' '}
                            <span className="highlight">real time</span> cycling statistics. The connection needed to be
                            easy, quick and wireless.
                        </p>
                        <p>
                            The user wants to see <span className="highlight">useful statistics</span> such as calories
                            burned, wattage, speed and so on. To achieve this we needed to design an algorithm to
                            convert the sensor data from the NOHrD Bike to usable statistics capable of being shown in
                            the <span className="highlight">user interface</span>.
                        </p>
                        <p>
                            To take things a step further, we wanted to achieve an engaging and flexible user experience
                            which every user can enjoy. One part of this would be offering a special video experience,
                            transporting you from your exercise location to a bike ride in your favourite surroundings.
                        </p>
                    </div>
                </div>
            </WillAnimate>

            <WillAnimate as="article" className="content">
                <div className="container">
                    <div className="content-group">
                        <div className="content-group__background content-group__background--right" />
                        <div className="content__image">
                            <div className="image">
                                <div className="image__content">
                                    <Img
                                        alt="NOHrD bike electronics during the development"
                                        fluid={data.nohrdBikeBomb.childImageSharp.fluid}
                                    />
                                </div>
                            </div>
                        </div>
                        <div className="content__copy">
                            <h2>Reliable data with the right sensors</h2>
                            <p>
                                Together with NOHrD, our team got to work to find a{' '}
                                <span className="highlight">good combination</span> of hardware and software. We looked
                                for the right sensors, the right wireless radio and the right data protocol.
                            </p>
                            <p>
                                We convert the data that is transmitted from the sensors in the NOHrD Bike via{' '}
                                <span className="highlight">Bluetooth</span> into statistics relevant to the user in the{' '}
                                <span className="highlight">interface</span>. Accurate calculations of calories burned,
                                wattage, speed and distance enable the user to analyse a training session on the NOHrD
                                Bike properly. The option to connect an external heart rate monitor provides even more
                                information on the cyclist’s performance.
                            </p>
                        </div>
                    </div>
                </div>
            </WillAnimate>

            {/* Article calibration and 'synchronisation'  */}

            <WillAnimate as="article" className="content">
                <div className="container">
                    <div className="content__image content__image--right">
                        <div className="image">
                            <div className="image__content">
                                <Img
                                    alt="An overview of the onboarding of the NOHrD Bike application on an iPhone."
                                    fluid={data.nohrdIPhones.childImageSharp.fluid}
                                />
                            </div>
                        </div>
                    </div>
                    <div className="content__copy">
                        <h2>Interfaces that are easy to understand</h2>
                        <p>
                            There is a straightforward onboarding session that gives the user{' '}
                            <span className="highlight">effective guidance</span> on setting up the Bluetooth connection
                            between the NOHrD Bike and the application.
                        </p>
                        <p>
                            To maintain the flexibility of the application, the user then chooses the form of the
                            training themselves. For example, <span className="highlight">the user determines</span>{' '}
                            which statistics are shown during the cycling session. The user can also make use of a{' '}
                            <span className="highlight">virtual coach</span> who sets objectives and varies the
                            intensity of the training resulting in a <span className="highlight">satisfying</span>{' '}
                            exercise session.
                        </p>
                    </div>
                </div>
            </WillAnimate>

            {/* Article video (can be a shot of a good quality video cycling in the alps/san fran with some moving datapoints on the tablet */}

            <WillAnimate as="article" className="content">
                <div className="container">
                    <div className="content-group">
                        <div className="content-group__background content-group__background--right" />
                        <div className="content__image">
                            <div className="image">
                                <div className="image__content">
                                    <Img
                                        alt="An example of the interactive video in the NOHrD Bike app."
                                        fluid={data.nohrdBikeVideo.childImageSharp.fluid}
                                    />
                                </div>
                            </div>
                        </div>
                        <div className="content__copy">
                            <h2>New experiences with virtual bike rides</h2>
                            <p>
                                The large collection of <span className="highlight">virtual bike rides</span> all around
                                the world makes for exciting training sessions. Using the NOHrD Bike applications the
                                user is able, from any exercise location, to cycle straight through the Alps, around the
                                San Francisco Bay Area or through one of the many other cities and recreational areas.
                            </p>
                            <p>
                                This means that through the enriching applications for iPhone, iPad and Android the
                                NOHrD Bike is able to offer a <span className="highlight">unique, accessible</span>{' '}
                                experience for use at home.
                            </p>
                        </div>
                    </div>
                </div>
            </WillAnimate>

            {/* Process description */}

            <WillAnimate as="article" className="content">
                <div className="container">
                    <div className="content__display">
                        <h2>
                            {/* Make sure to have the correct spacing when br becomes invisible */}
                            An efficient <br />
                            process is <br />
                            essential
                        </h2>
                    </div>
                    <div className="content__copy">
                        <p className="p-lead">
                            An <span className="highlight">efficient process</span> and good working relationship are
                            essential when developing software, particularly software that seamlessly connects with a
                            physical product.
                        </p>
                        <p>
                            For the NOHrD Bike apps, we use <span className="highlight">agile working practices</span>.
                            We take an iterative approach. With a delivery at the end of each two week iteration. We
                            call these iterations sprints. Each delivery brings the software a step closer to being a
                            perfect product.
                        </p>
                        <p>
                            A software product is never finished and this is equally true of the NOHrD Bike apps. So we{' '}
                            <span className="highlight">regularly</span> develop the software further. We keep refining
                            the product and ensure that the apps continue to function perfectly following platform
                            changes. Our continual intensive cooperation with the product owner at NOHrD means that
                            there are no surprises and the right objectives are achieved.
                        </p>
                    </div>
                </div>
            </WillAnimate>

            {/* Article image and role */}

            <WillAnimate as="article" className="content">
                <div className="image">
                    <div className="image__content">
                        <Img
                            alt="The NOHrD Bike next to other NOHrD products."
                            fluid={data.nohrdBikeRoom.childImageSharp.fluid}
                        />
                    </div>
                </div>
            </WillAnimate>

            <WillAnimate as="article" className="content">
                <div className="container">
                    <div className="content__display">
                        <h2>
                            {/* Make sure to have the correct spacing when br becomes invisible */}
                            Want to <br /> enrich <br /> a physical <br /> product too?
                        </h2>
                    </div>
                    <div className="content__copy">
                        <p className="p-lead">
                            For NOHrD, software became an important part of the product experience. Even more
                            importantly&mdash;we built up a <span className="highlight">long-term relationship</span> in
                            the process. If you have an interesting idea then get in touch with{' '}
                            <span className="inline-avatar">
                                <Img alt="Joris" fixed={data.joris.childImageSharp.fixed} Tag="span" />
                                <span>Joris</span>
                            </span>{' '}
                            to discuss your plans.
                        </p>
                    </div>
                    <div className="content__buttons content__buttons--50">
                        <Button
                            url="/en/contact/"
                            name="Contact us"
                            title="Contact information Enschede and Amsterdam"
                        />
                    </div>
                </div>
            </WillAnimate>
        </section>
    </Layout>
);

export const query = graphql`
    query {
        nohrdBike: file(relativePath: {eq: "portfolio/nohrd-bike/nohrd-bike.jpg"}) {
            ...fullscreenHero
        }

        joris: file(relativePath: {eq: "people/joris_2_zoom.jpg"}) {
            ...inlineAvatarImage
        }

        nohrdBikeDevices: file(relativePath: {eq: "portfolio/nohrd-bike/nohrd-bike-devices.png"}) {
            ...fullscreenHero
        }
        nohrdBikeBomb: file(relativePath: {eq: "portfolio/nohrd-bike/nohrd-bike-bomb.jpg"}) {
            ...fluid800Width
        }
        nohrdIPhones: file(relativePath: {eq: "portfolio/nohrd-bike/nohrd-bike-iphones.png"}) {
            ...fluid800Width
        }
        nohrdBikeVideo: file(relativePath: {eq: "portfolio/nohrd-bike/nohrd-bike-video.jpg"}) {
            ...fluid1200Width
        }
        nohrdBikeRoom: file(relativePath: {eq: "portfolio/nohrd-bike/nohrd-bike-room.jpg"}) {
            ...fluid1200Width
        }
    }
`;
